.acc-billing {}
.acc-billing__form {
    position: relative;
    .acc-billing__form-field {
        border: 1px solid #979797;
        border-radius: 8px;
        height: 68px;
        background-color: #fff;
        font-size: 18px;
        line-height: 28px;
        width: 100%;
        @include r($wide) {
            height: 60px;
            font-size: 14px;
            line-height: normal; }
        @include r($desktop) {
            height: 52px; }
        @include r($mobile) {
            height: 46px; } } }
.acc-billing__form-btn {
    position: absolute;
    width: 20px;
    height: 20px;
    border: none;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    cursor: pointer;
    svg {
        width: 100%;
        height: 100%;
        fill: #979797; } }
.acc-billing__inner {
    margin-top: 30px;
    @include r($mobile) {
        margin-top: 20px; } }
.acc-billing__acc-billing-item {
    margin-top: 16px;
    &:first-child {
        margin-top: 0; } }

.acc-billing__empty {
    margin-top: 136px;
    text-align: center;
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    text-align: center;
    letter-spacing: -0.005em;
    color: #979797;
    @include r($wide) {
        margin-top: 80px;
        font-size: 25px;
        line-height: normal; }
    @include r($desktop) {
        margin-top: 20px;
        font-size: 19px; } }

.acc-billing_empty {
    .acc-billing__form-btn {
        svg {
            fill: #D32F2F; } } }
