input[type="text"],
input[type="tel"],
input[type="email"],
input[type="password"],
textarea, {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
  border: none;
  background: #EFEFEF;
  border-radius: 6px;
  padding-left: 28px;
  padding-right: 28px;

  font-family: 'Poppins';
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.2px;
  color: #212121;
  @include r($mobile) {
    padding-left: 14px;
    padding-right: 14px; }
  &::-webkit-input-placeholder {}

  &::-moz-placeholder {}

  &:-ms-input-placeholder {} }
input[type="text"],
input[type="tel"],
input[type="email"],
input[type="password"],
select, {
  height: 52px;
  @include r($mobile) {
    height: 46px; } }
/*  Firefox */
input[type=number] {
  -moz-appearance: textfield; }
textarea {
  resize: none;
  height: 100px;
  padding-top: 14px; }
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
  border: none;
  background: url(/images/arrow-down.svg) no-repeat right 20px center / 12px auto;
  background-color: #EFEFEF;
  border-radius: 6px;
  padding-left: 28px;
  padding-right: 28px;

  font-family: Poppins;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.2px;
  color: #212121;
  @include r($mobile) {
    padding-left: 14px;
    padding-right: 14px; } }
.btn {
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.2px;
  color: #FFFFFF;
  cursor: pointer;
  height: 52px;
  background: #D32F2F;
  box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
  border-radius: 39px;
  transition: 0.3s;
  &:hover {
    background: rgba(#EFEFEF,0.4);
    box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
    color: rgba(#212121,0.4); }
  @include r($mobile) {
    height: 46px;
    letter-spacing: 0;
    font-size: 15px; } }

.custom-checkbox, .custom-radio {
  cursor: pointer;
  input {
    position: absolute;
    opacity: 0;
    z-index: -1;
    &:checked {
      & + .custom-checkbox__txt, & + .custom-radio__txt {
        &::after {
          opacity: 1;
          transform: scale(1); }
        &::before {
          border-color: #D32F2F; } } } }
  .custom-checkbox__txt, .custom-radio__txt {
    display: flex;
    align-items: center;
    user-select: none;
    position: relative;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #000000;
    &::before {
      content: '';
      display: block;
      min-width: 20px;
      max-width: 20px;
      height: 20px;
      border: 1px solid #212121;
      border-radius: 50%;
      margin-right: 12px; }
    &::after {
      content: url(/images/check.svg);
      width: 12px;
      position: absolute;
      top: 0;
      left: 4px;
      transition: 0.3s;
      transform: scale(0.5);
      opacity: 0; } } }


.datepicker-top-left, .datepicker-top-right {
  border-top-color: #D32F2F;
  &::before {
    border-bottom-color: #D32F2F; } }
.datepicker-panel>ul>li.picked, .datepicker-panel>ul>li.picked:hover {
  background: #D32F2F;
  color: #fff; }

input.is-invalid {
  border: 1px solid #D32F2F; }
.invalid-feedback {
  margin-top: 10px;
  color: #D32F2F;
  font-weight: 500; }
