.acc-change-password {
    max-width: 370px;
    width: 100%; }
.acc-change-password__form-field {
    margin-top: 20px;
    &:first-child {
        margin-top: 0; } }
.acc-change-password__btn {
    padding-left: 36px;
    padding-right: 36px;
    margin-top: 28px; }


.acc-change-password__photo {
    display: flex;
    align-items: center;
    overflow: hidden;
    position: relative;
    margin-bottom: 40px; }
.acc-change-password__photo-img {
    width: 96px;
    height: 96px;
    border-radius: 16px;
    margin-right: 24px;
    object-fit: contain; }
.acc-change-password__change-photo {
    input[type="file"] {
        position: absolute;
        z-index: -1;
        opacity: 0; } }
.acc-change-password__txt {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    text-transform: uppercase;
    cursor: pointer; }
