.account-content {
    width: 100%;
    border-left: 2px solid #EFEFEF;
    min-height: 100%;
    padding-top: 20px;
    padding-left: 30px;
    @include r($wide) {
        padding-top: 10px;
        padding-left: 10px; }
    @include r($desktop) {
        padding-left: 20px;
        padding-right: 20px;
        padding-top: 30px;
        border-top: 2px solid #EFEFEF;
        margin-top: 30px;
        border-left: 0; }
    @include r($mobile) {
        padding-left: 0;
        padding-right: 0; } }
