.login {
    margin-top: calc( var(--headerHeight) + 24px );
    margin-bottom: 120px;
    overflow: hidden;
    @include r($wide) {
        margin-top: calc( var(--headerHeight) + 50px );
        margin-bottom: 100px; }
    @include r($desktop) {
        margin-bottom: 70px; }
    @include r($mobile) {
        margin-bottom: 50px; } }
.login__container {
    position: relative;
    min-height: 696px;
    @include r($wide) {
        min-height: auto; }
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 100%;
        transform: translateX(-395px);
        height: 100%;
        width: 50vw;
        background: #D32F2F;
        border-radius: 10px 0 0 10px;
        @include r($wide) {
            display: none; } }
    &::after {
        position: absolute;
        content: url(/images/login-decor.png);
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
        @include r($wide) {
            content: '';
            display: none; } } }
.login__section-title {
    text-align: left; }
.login__inner {
    padding-top: 83px;
    @include r($wide) {
        padding-top: 0; } }

.login__go-back {
    width: 48px;
    display: block;
    margin-bottom: 48px;
    @include r($tablet) {
        margin-bottom: 30px; }
    &::before {
        content: url(/images/arrow-left.svg);
        width: 100%;
        opacity: 0.9;
        transition: 0.3s opacity; }
    &:hover {
        &::before {
            opacity: 1; } } }
.login__txt {
    margin-top: 15px;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #212121; }

.login__form-login {
    margin-top: 45px;
    @include r($mobile) {
        margin-top: 20px; } }
.login__form-forgot-password {
    margin-top: 18px; }
