.account {
    padding-top: 112px;
    padding-bottom: 216px;
    @include r($wide) {
        padding-top: 80px;
        padding-bottom: 150px; }
    @include r($desktop) {
        padding-bottom: 110px;
        padding-top: 60px; }
    @include r($tablet) {
        padding-top: 50px;
        padding-bottom: 70px; } }
.account__top-line {
    margin-bottom: 28px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r($mobile) {
        flex-direction: column;
        align-items: flex-start; } }
.account__section-title {
    text-align: left; }
.account__balance {
    padding: 7px 12px;
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    letter-spacing: -0.005em;
    color: #D32F2F;
    background: #F9F9F9;
    border-radius: 16px;
    @include r($mobile) {
        margin-top: 15px;
        padding: 5px 10px;
        font-size: 24px;
        line-height: normal;
        border-radius: 5px; } }
