.form-contact-us {
    max-width: 453px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    .form-contact-us__field {
        width: 62.69%; }
    .form-contact-us__btn {
        width: 35.09%; } }
.form-contact-us__invalid {
    display: none;
    position: absolute;
    top: calc( 100% + 4px );
    left: 0;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.2px;
    color: #D32F2F; }
.form-contact-us_invalid {
    .form-contact-us__field {
        border: 1px solid #D32F2F; }
    .form-contact-us__invalid {
        display: block; } }
