.account-sidebar {
    width: 100%;
    background: #F9F9F9;
    border-radius: 20px;
    padding: 20px;
    @include r($wide) {
        padding: 10px; } }
.account-sidebar__wrap-item {
    margin-top: 16px;
    &:first-child {
        margin-top: 0; } }
.account-sidebar__item {
    background: #FFFFFF;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include r($wide) {
        padding: 10px; } }
.account-sidebar__link {
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #212121;
    margin-right: 8px;
    @include r($wide) {
        font-size: 17px;
        line-height: normal; } }
.account-sidebar__btn {
    &::before {
        content: '+';
        font-family: 'Poppins';
        font-weight: 600;
        font-size: 18px;
        line-height: 28px;
        color: #212121; } }
.account-sidebar__requests {
    margin-left: auto;
    margin-right: 12px;

    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    color: #D32F2F;
    @include r($wide) {
        margin-right: 6px; } }
.account-sidebar__sublist {
    padding: 0 20px;
    display: none;
    @include r($wide) {
        padding: 0 10px; } }
.account-sidebar__subitem {
    margin-top: 16px;
    @include r($wide) {
        margin-top: 14px; } }
.account-sidebar__sublink {
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    @include r($wide) {
        font-size: 17px;
        line-height: normal; } }
.account-sidebar__item_current {
    .account-sidebar__link {
        color: #D32F2F; }
    .account-sidebar__btn {
        &::before {
            color: #D32F2F; } } }
.account-sidebar__sublink_current {
    color: #D32F2F; }

.account-sidebar__wrap-item_open {
    .account-sidebar__btn {
        &::before {
            content: '-'; } } }
