.contact__section-title {
    text-align: left; }
.contact__subtitle {
    margin-top: 18px;

    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    color: #212121;
    @include r($tablet) {
        margin-top: 16px; } }
.contact__txts {
    margin-top: 24px;
    @include r($tablet) {
        margin-top: 16px; } }
.contact__txt, .contact__txt a {
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    color: #212121; }
.contact__txt {
    margin-top: 3px;
    &:first-child {
        margin-top: 0; }
    strong {
        font-weight: 500; }
    a {
        &:hover {
            text-decoration: underline; } } }
.contact__form-contact {
    margin-top: 48px;
    @include r($desktop) {
        margin-top: 40px; }
    @include r($tablet) {
        margin-top: 30px; } }
