.p-how-it-works__inner {
  margin-top: 40px; }

.p-how-it-works__subtitle, .p-how-it-works__subsubtitle {
  color: #212121; }
.p-how-it-works__subtitle {
  font-size: 30px;
  @include r($mobile) {
    font-size: 23px; } }
.p-how-it-works__subsubtitle {
  margin-top: 10px;
  font-size: 20px;
  font-weight: normal;
  @include r($mobile) {
    font-size: 18px; } }
.p-how-it-works__inner__step-by-step {
  margin-top: 30px; }
.p-how-it-works__content {
  margin-top: 60px;
  @include r($mobile) {
    margin-top: 40px; }
  h4 {
    font-weight: 500;
    font-size: 20px;
    color: #D32F2F;
    text-transform: uppercase;
    margin-top: 30px;
    &:first-child {
      margin-top: 0; } }
  p {
    margin-top: 10px;
    font-size: 16px;
    &:first-child {
      margin-top: 0; } } }
