.form-contact {
    max-width: 820px;
    width: 100%; }
.form-contact__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .form-contact__form-field {
        width: calc( 50% - 40px );
        @include r($tablet) {
            width: calc( 50% - 15px ); }
        @include r($mobile) {
            width: 100%; }
        @include rmin($mobile+1) {
            &:nth-child(-n+2) {
                margin-top: 0; } }
        @include r($mobile) {
            &:first-child {
                margin-top: 0; } } } }
.form-contact__form-field {
    margin-top: 40px;
    @include r($desktop) {
        margin-top: 20px; } }
.form-contact__btn {
    padding-left: 36px;
    padding-right: 36px;
    margin-top: 48px;
    @include r($desktop) {
        margin-top: 35px; }
    @include r($mobile) {
        margin-top: 20px; } }

