.stars-rating {
    height: 24px;
    width: 154px;
    background-image: url(/images/star_empty_v2.png);
    background-repeat: repeat-x;
    background-position: left center;
    position: relative;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 100%;

        background-image: url(/images/star_full_v2.png);
        background-repeat: repeat-x;
        background-position: left center; } }

.stars-rating_fullness_0-5 {
    &::before {
        width: 13px; } }
.stars-rating_fullness_1 {
    &::before {
        width: 26px; } }
.stars-rating_fullness_1-5 {
    &::before {
        width: 45px; } }
.stars-rating_fullness_2 {
    &::before {
        width: 57px; } }
.stars-rating_fullness_2-5 {
    &::before {
        width: 77px; } }
.stars-rating_fullness_3 {
    &::before {
        width: 90px; } }
.stars-rating_fullness_3-5 {
    &::before {
        width: 109px; } }
.stars-rating_fullness_4 {
    &::before {
        width: 122px; } }
.stars-rating_fullness_4-5 {
    &::before {
        width: 141px; } }
.stars-rating_fullness_5 {
    &::before {
        width: 155px; } }
