.feedback-form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 818px;
    width: 100%; }
.feedback-form__form-field {
    width: 100%;
    margin-top: 45px;
    @include r($tablet) {
        margin-top: 30px; } }
.feedback-form__btn {
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 45px;
    @include r($tablet) {
        margin-top: 30px; } }
.feedback-form__rating {
    display: flex;
    flex-direction: row-reverse;
    input {
        position: absolute;
        z-index: -1;
        opacity: 0; }
    label {
        margin-left: 8px;
        cursor: pointer;
        &:last-child {
            margin-left: 0; } } }
.feedback-form__rating > label:before {
    display: block;
    content: '';
    width: 30px;
    height: 30px;
    background-repeat: no-repeat;
    background-image: url(/images/star_empty_v3.png);
    background-size: 100% 100%;
    background-position: center; }
.feedback-form__rating input:checked ~ label:before {
    background-image: url(/images/star_full_v3.png); }
