.page-title-sec {
    margin-top: var(--headerHeight);
    padding: 70px 0;
    background: #D32F2F;
    @include r($wide) {
        padding: 60px 0; }
    @include r($desktop) {
        padding: 50px 0; }
    @include r($tablet) {
        padding: 40px 0; }
    @include r($mobile) {
        padding: 30px 0; } }
.page-title-sec__title {
    font-weight: bold;
    font-size: 72px;
    line-height: 84px;
    text-align: center;
    letter-spacing: -0.005em;
    text-transform: capitalize;
    color: #FFFFFF;
    @include r($wide) {
        font-size: 65px;
        line-height: normal; }
    @include r($desktop) {
        font-size: 55px; }
    @include r($tablet) {
        font-size: 45px; }
    @include r($mobile) {
        font-size: 35px; } }
