.acc-billing-item {
    width: 100%;
    min-height: 186px;
    background: #F9F9F9;
    border-radius: 20px;
    padding: 24px 32px 24px 24px;
    position: relative;
    @include r($wide) {
        min-height: auto; }
    @include r($mobile) {
        padding: 10px;
        border-radius: 10px; } }
.acc-billing-item__top-line {
    display: flex;
    align-items: center;
    @include r($mobile) {
        flex-direction: column;
        align-items: flex-start; } }
.acc-billing-item__btn-info {
    position: absolute;
    top: 32px;
    right: 32px;
    width: 20px;
    height: 20px;
    background: url(/images/more-info.svg) no-repeat center / 20px 20px;
    @include r($mobile) {
        top: 20px;
        right: 20px; } }
.acc-billing-item__title {
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    color: #212121;
    letter-spacing: -0.005em;
    @include r($wide) {
        font-size: 23px;
        line-height: normal; } }
.acc-billing-item__date {
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    color: #212121;
    margin-left: 20px;
    margin-top: 6px;
    @include r($wide) {
        margin-left: 10px;
        margin-top: 2px; }
    @include r($mobile) {
        margin-left: 0;
        margin-top: 4px; } }
.acc-billing-item__path {
    margin-top: 16px;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    color: #212121;
    @include r($wide) {
        font-size: 20px;
        line-height: normal; } }
.acc-billing-item__characteristic {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
    @include r($mobile) {
        margin-top: 0;
        flex-direction: column;
        align-items: flex-start; } }
.acc-billing-item__characteristic_actions {
    @include r($wide) {
        margin-top: 0;
        flex-direction: column;
        align-items: flex-start; } }
.acc-billing-item__actions {
    display: flex;
    align-items: center;
    @include r($wide) {
        margin-top: 8px; } }
.acc-billing-item__btn-accept {
    margin-right: 20px;
    color: #D32F2F;
    &::after {
        background-size: 14px 14px;
        background-image: url(/images/check_v2.svg); } }
.acc-billing-item__btn-cancel {
    color: #979797;
    &::after {
        background-size: 12px 12px;
        background-image: url(/images/x_v2.svg); } }
.acc-billing-item__btn-accept, .acc-billing-item__btn-cancel {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    display: flex;
    align-items: center;
    &::after {
        content: '';
        width: 14px;
        height: 14px;
        margin-left: 10px;
        background-repeat: no-repeat;
        background-position: center; } }
.acc-billing-item__characteristic-txt {
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    color: #212121;
    margin-right: 10px; }
.acc-billing-item__download {
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    display: flex;
    align-items: center;
    @include r($mobile) {
        font-size: 14px;
        line-height: 14px;
        margin-top: 5px; }
    &::after {
        content: url(/images/arrow-right.svg);
        width: 18px;
        margin-left: 12px;
        margin-top: 2px; } }
.acc-billing-item__actions2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 24px;
    @include r($wide) {
        flex-direction: column;
        align-items: flex-start; } }
.acc-billing-item__slide-content2 {
    margin-top: 36px;
    display: none;
    @include r($wide) {
        margin-top: 20px; } }
.acc-billing-item__btn-offers-company {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    &::after {
        content: '+';
        margin-left: 2px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 0.2px; } }
.acc-billing-item__btn-offers-company_open {
    &::after {
        content: '-'; } }
.acc-billing-item__btn-cancel2 {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    text-transform: uppercase;
    letter-spacing: 0.2px;
    color: #D32F2F;
    display: flex;
    align-items: center;
    margin-left: auto;
    @include r($wide) {
        margin-top: 10px; }
    svg {
        fill: #D32F2F;
        width: 16px;
        height: 16px;
        margin-left: 13px;
        margin-bottom: 2px; } }
.acc-billing-item__btn-cancel2_canceled {
    color: #979797;
    svg {
        display: none; } }
.acc-billing-item__slide-content {
    margin-top: 31px;
    display: none;
    @include r($wide) {
        margin-top: 20px; } }
.acc-billing-item__slide-content-row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 20px;
    &:first-child {
        margin-top: 0; }
    @include r($wide) {
        flex-direction: column;
        align-items: flex-start;
        margin-top: 14px; } }
.acc-billing-item__slide-content-l {
    padding-right: 10px;

    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($wide) {
        font-size: 16px;
        line-height: normal;
        padding-right: 0; } }
.acc-billing-item__slide-content-r {
    max-width: 443px;
    width: 100%;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($wide) {
        font-size: 16px;
        line-height: normal; } }
.acc-billing-item_open {
    .acc-billing-item__btn-info {
        background: url(/images/x_v2.svg) no-repeat center / 16px 16px; } }
