.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0); }
html.mfp-helper {
  height: 100%;
  body {
    overflow: hidden;
    height: 100%;
    -webkit-transform: translateZ(0); } }
.mfp-close-btn-in .mfp-close {
  top: 10px;
  right: 10px;
  opacity: 1;
  width: 30px;
  height: 30px;
  color: transparent;
  background: url(/images/x.svg) no-repeat center; }
.white-popup-block {
  max-width: 730px;
  width: 100%;
  padding: 20px 40px;
  margin: 0 auto;
  position: relative;
  background: #F9F9F9;
  border-radius: 20px; }
.popup-buble {
  .popup-dialog {
    opacity: 0;
    transition: all 0.2s ease-in-out;
    transform: scale(0.8); }
  &.mfp-ready .popup-dialog {
    opacity: 1;
    transform: scale(1); }
  &.mfp-removing .popup-dialog {
    transform: scale(0.8);
    opacity: 0; }
  &.mfp-bg {
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: 0.3s; }
  &.mfp-ready.mfp-bg {
    opacity: 1; }
  &.mfp-removing.mfp-bg {
    opacity: 0; } }

.confirm-deletion {
  max-width: 450px; }
.confirm-deletion__txt {
  text-align: center;
  font-size: 20px;
  font-weight: 400; }
.confirm-deletion__wrap {
  margin-top: 20px;
  display: flex;
  justify-content: center; }
.confirm-deletion__btn-l {
  margin-right: 30px;

  font-size: 16px;
  font-weight: 600; }
.confirm-deletion__btn-r {
  height: 48px;
  padding-left: 20px;
  padding-right: 20px; }
