.form-login {
    display: flex;
    flex-direction: column; }
.form-login__form-field {
    margin-top: 20px;
    &:first-child {
        margin-top: 0; } }
.form-login__forgot-pas {
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #D32F2F;
    margin-top: 18px;
    &:hover {
        text-decoration: underline; } }
.form-login__checkbox {
    cursor: pointer;
    margin-top: 28px;
    @include r($mobile) {
        margin-top: 12px; }
    input[type="checkbox"] {
        position: absolute;
        opacity: 0;
        z-index: -1;
        &:checked {
            & + .form-login__checkbox-txt {
                &::after {
                    opacity: 1;
                    transform: scale(1); }
                &::before {
                    border-color: #D32F2F; } } } }
    .form-login__checkbox-txt {
        display: flex;
        align-items: center;
        user-select: none;
        position: relative;

        font-weight: normal;
        font-size: 18px;
        line-height: 28px;
        letter-spacing: -0.005em;

        color: #000000;

        &::before {
            content: '';
            display: block;
            width: 20px;
            height: 20px;
            border: 1px solid #212121;
            border-radius: 50%;
            margin-right: 12px; }
        &::after {
            content: url(/images/check.svg);
            width: 12px;
            position: absolute;
            top: 0;
            left: 4px;
            transition: 0.3s;
            transform: scale(0.5);
            opacity: 0; } } }
.form-login__btn {
    margin-top: 18px;
    max-width: 154px; }
.form-login__become-partner {
    margin-top: 18px;

    font-weight: normal;
    font-size: 18px;
    line-height: 28px;

    text-transform: capitalize;

    color: #212121;
    a {
        color: #D32F2F;
        font-weight: 600;
        &:hover {
            text-decoration: underline; } } }
