.give-feedback__section-title {
    text-align: left; }
.give-feedback__logo-company {
    margin-top: 45px;
    max-width: 200px;
    height: auto;
    display: block; }
.give-feedback__title-company {
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    color: #212121;
    margin-top: 20px; }
.give-feedback__contact {
    margin-top: 23px;
    display: flex;
    flex-direction: column; }
.give-feedback__contact-item {
    margin-top: 16px;
    padding-left: 44px;
    position: relative;
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.005em;
    text-transform: capitalize;
    color: #212121;
    @include r($mobile) {
        padding-left: 0;
        font-size: 18px;
        line-height: normal; }
    &:first-child {
        margin-top: 0; }
    &::before {
        width: 30px;
        position: absolute;
        top: calc( 50% + 5px );
        left: 0;
        transform: translateY(-50%);
        @include r($mobile) {
            display: none; } } }
.give-feedback__contact-item_tel {
    &::before {
        content: url(/images/call_red.svg);
        @include r($mobile) {
            content: ''; } } }
.give-feedback__contact-item_email {
    &::before {
        content: url(/images/msg_red.svg);
        @include r($mobile) {
            content: ''; } } }
.give-feedback__feedback-form {
    margin-top: 48px;
    @include r($mobile) {
        margin-top: 27px; } }

