.reviews {
    margin-top: 120px; }
.reviews__l {
    position: relative;
    padding-top: 66px;
    @include r($desktop) {
        padding-top: 30px;
        padding-bottom: 30px;
        margin-bottom: 20px; }
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        width: 50vw;
        height: 694px;
        background: #D32F2F;
        border-radius: 0px 10px 10px 0px;
        @include r($desktop) {
            height: 100%;
            width: 100vw;
            right: 15px; } } }
.reviews__section-title {
    text-align: left;
    color: #fff;
    padding-right: 67px;
    position: relative;
    @include r($desktop) {
        padding-right: 50px; } }
.reviews__r-inner {
    padding-right: 30px;
    max-height: 694px;
    .simplebar-track.simplebar-vertical {
        width: 4px;
        border-radius: 4px; }
    .simplebar-scrollbar:before {
        left: 0;
        right: 0;
        opacity: 1;
        background-color: #D32F2F; }
    .simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
        top: 0;
        bottom: 0; } }
.reviews__review {
    margin-top: 48px;
    &:first-child {
        margin-top: 0; } }





