.acc-bank-card {
    .acc-bank-card__inner {
        max-width: 360px;
        width: 100%;
        & > .acc-bank-card__form-field {
            &:first-child {
                margin-top: 0; } } }
    .acc-bank-card__form-field {
        margin-top: 40px;
        @include r($tablet) {
            margin-top: 20px; } }
    .acc-bank-card__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        max-width: 304px;
        width: 100%;
        .acc-bank-card__form-field {
            width: calc( 50% - 12px ); } } }
.acc-bank-card__btn {
    padding-left: 30px;
    padding-right: 30px; }
