.temp-form-steps {
    .steps-forms__block {
        margin-top: 35px;
        &:first-child {
            margin-top: 0; } }
    .steps-forms__wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width: 820px;
        width: 100%;
        @include r($mobile) {
            flex-direction: column; } }
    .form-field {
        width: calc( 50% - 40px );
        margin-top: 40px;
        @include r($desktop) {
            width: calc(50% - 15px);
            margin-top: 20px; }
        @include r($mobile) {
            width: 100%; }
        @include rmin($mobile+1) {
            &:nth-child(-n+2) {
                margin-top: 0; } }
        @include r($mobile) {
            &:first-child {
                margin-top: 0; } } }
    .form-field_full {
        width: 100%;
        max-width: 820px; }
    .steps-form__title + .steps-forms__wrap {
        margin-top: 22px; }
    .steps-form__checkboxes {
        .row {
            & > div {
                margin-top: 18px; } } }
    .prev-step {
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 150px;
        background-color: #979797;
        padding-left: 30px;
        padding-right: 30px;
        font-family: 'Poppins';
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 0.2px;
        color: #FFFFFF;
        cursor: pointer;
        height: 52px;
        box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
        border-radius: 39px;
        transition: 0.3s;
        &:hover {
            background-color: #c5c5c5; }
        @include r($mobile) {
            height: 46px;
            letter-spacing: 0;
            font-size: 15px; } } }
