.faq__section-title {
    text-align: left;
    margin-top: 48px;
    @include r($tablet) {
        margin-top: 30px; }
    @include r($mobile) {
        margin-top: 25px; } }
.faq__inner {
    margin-top: 20px; }
.faq__txt {
    margin-top: 48px;

    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($tablet) {
        margin-top: 30px;
        font-size: 16px;
        line-height: normal; } }
.faq__form-faq {
    margin-top: 30px;
    max-width: 600px;
    width: 100%;
    @include r($tablet) {
        margin-top: 20px; } }
