.advantages {
    margin-top: 200px;
    @include r($wide) {
        margin-top: 80px; }
    @include r($tablet) {
        margin-top: 60px; } }
.advantages__inner {
    margin-top: 75px;
    @include r($desktop) {
        justify-content: center; }
    @include r($tablet) {
        margin-top: 30px; } }
.advantages__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    @include r($desktop) {
        margin-top: 50px; }
    @include r($tablet) {
        margin-top: 25px; }
    &:first-child {
        @include r($wide) {
            margin-top: 0; } } }
.advantages__item-img {
    max-width: 100%; }
.advantages__item-title {
    margin-top: 35px;

    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    text-transform: capitalize;
    color: #212121;
    @include r($mobile) {
        font-size: 20px;
        line-height: normal; } }
.advantages__item-txt {
    margin-top: 15px;

    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($mobile) {
        font-size: 16px;
        line-height: normal; } }
