.acc-offers-company__item {
    margin-top: 43px;
    &:first-child {
        margin-top: 0; } }
.acc-offers-company__row {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 8px;
    @include r($wide) {
        flex-direction: column; }
    &:first-child {
        margin-top: 0; } }
.acc-offers-company__l, .acc-offers-company__r {
    width: 50%;
    @include r($wide) {
        width: 100%; } }
.acc-offers-company__l {
    padding-right: 10px;
    @include r($wide) {
        padding-right: 0; } }
.acc-offers-company__r {
    display: flex;
    flex-direction: column;
    @include r($wide) {
        margin-top: 8px; } }
.acc-offers-company__r_right {
    flex-direction: row;
    justify-content: flex-end;
    align-self: center;
    @include r($wide) {
        align-self: flex-start;
        justify-content: flex-start; } }
.acc-offers-company__img {
    max-width: 100%;
    height: auto;
    display: block; }
.acc-offers-company__link-profile {
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    display: flex;
    align-items: center;
    @include r($mobile) {
        font-size: 14px;
        line-height: 14px;
        margin-top: 5px; }
    &::after {
        content: url(/images/arrow-right.svg);
        width: 18px;
        margin-left: 12px;
        margin-top: 2px; } }
.acc-offers-company__title, .acc-offers-company__location {
    font-weight: normal;
    font-size: 18px;
    line-height: 28px; }
.acc-offers-company__title {
    color: #212121; }
.acc-offers-company__location {
    color: #757575; }
.acc-offers-company__stars-rating {
    display: flex;
    align-items: center;
    margin-top: 12px;
    @include r($mobile) {
        flex-direction: column;
        align-items: flex-start;
        margin-top: 8px; } }
.acc-offers-company__stars-rating-txt {
    margin-left: 17px;
    margin-top: 3px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    @include r($mobile) {
        margin-left: 0;
        margin-top: 4px; } }
.acc-offers-company__contact-item {
    padding-left: 40px;
    position: relative;
    font-family: 'Poppins';
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    text-transform: capitalize;
    color: #212121;
    @include r($mobile) {
        padding-left: 0; }
    &::before {
        width: 30px;
        position: absolute;
        top: calc( 50% + 3px );
        left: 0;
        transform: translateY(-50%);
        @include r($mobile) {
            display: none; } } }
.acc-offers-company__contact-item_tel {
    &::before {
        content: url(/images/call_gray.svg);
        @include r($mobile) {
            content: ''; } } }
.acc-offers-company__contact-item_tel_email {
    &::before {
        content: url(/images/msg_gray.svg);
        @include r($mobile) {
            content: ''; } } }
