.steps-indicators_margin_top {
    margin-top: 80px;
    @include r($desktop) {
        margin-top: 60px; }
    @include r($tablet) {
        margin-top: 40px; }
    @include r($mobile) {
        margin-top: 30px; } }
.steps-indicators__wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    padding-bottom: 40px;
    background: #efefef;
    border-radius: 40px;
    counter-reset: i;
    position: relative;
    overflow: hidden;
    &::before {
        content: '';
        width: 100%;
        left: 0;
        height: 4px;
        background-color: #fff;
        position: absolute;
        top: 62px;
        @include r($mobile) {
            top: 39px; } }
    @include r($mobile) {
        padding-top: 20px;
        padding-bottom: 20px; } }
.steps-indicators__wrap-3-steps {
    max-width: 820px; }
.steps-indicators__item {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
    position: relative;
    @include r($desktop) {
        padding-left: 0;
        padding-right: 0; }
    &:first-child {
        &::before {
            content: '';
            height: 4px;
            width: 100%;
            background-color: #efefef;
            position: absolute;
            top: 22px;
            right: 50%;
            @include r($mobile) {
                top: 19px; } } }
    &:last-child {
        &::before {
            content: '';
            height: 4px;
            width: 100%;
            background-color: #efefef;
            position: absolute;
            top: 22px;
            left: 50%;
            @include r($mobile) {
                top: 19px; } } } }
.steps-indicators__counter {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #979797;
    transition: 0.3s background;
    position: relative;
    @include r($mobile) {
        width: 40px;
        height: 40px; }
    &::before {
        content: counter(i);
        counter-increment: i;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        color: #FFFFFF;
        @include r($mobile) {
            font-size: 20px;
            line-height: 20px; } } }
.steps-indicators__txt {
    margin-top: 32px;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.2px;
    color: #212121;
    @include r($desktop) {
        display: none; } }
.steps-indicators__wrap_3-steps {
    max-width: 820px; }
.steps-indicators__item_active {
    .steps-indicators__counter {
        background: #D32F2F; } }
