.steps-desc {
    padding-top: 100px;
    @include r($wide) {
        padding-top: 70px; }
    @include r($desktop) {
        padding-top: 50px; }
    @include r($tablet) {
        padding-top: 40px; } }
.steps-desc__section-title {
    text-align: left;
    margin-top: 45px;
    @include r($desktop) {
        margin-top: 30px; }
    @include r($tablet) {
        margin-top: 20px; } }
.steps-desc__subtitle {
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    letter-spacing: -0.005em;
    color: #212121;
    margin-top: 15px;
    @include r($tablet) {
        font-size: 20px;
        line-height: normal; } }
.steps-desc__txt {
    margin-top: 15px;

    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($tablet) {
        font-size: 16px;
        line-height: normal; } }
