.acc-company-profile__wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .form-field {
        width: calc( 50% - 10px );
        margin-top: 40px;
        @include rmin($mobile+1) {
            &:nth-child(-n+2) {
                margin-top: 0; } }
        @include r($mobile) {
            width: 100%;
            margin-top: 20px;
            &:first-child {
                margin-top: 0; } } } }
.acc-company-profile__block {
    margin-top: 42px;
    @include r($mobile) {
        margin-top: 20px; }
    &:first-child {
        margin-top: 0; }
    .row {
        & > div {
            margin-top: 20px;
            @include rmin($mobile+1) {
                &:nth-child(-n+2) {
                    margin-top: 0; } }
            @include r($mobile) {
                &:first-child {
                    margin-top: 0; } } } }
    .btn {
        padding-left: 30px;
        padding-right: 30px; } }
.acc-company-profile__title {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    color: #212121;
    margin-bottom: 20px; }

.acc-company-profile__photo {
    display: flex;
    align-items: center;
    overflow: hidden;
    position: relative; }
.acc-company-profile__photo-img {
    width: 96px;
    height: 96px;
    border-radius: 16px;
    margin-right: 24px;
    object-fit: contain; }
.acc-company-profile__change-photo {
    input[type="file"] {
        position: absolute;
        z-index: -1;
        opacity: 0; } }
.acc-company-profile__txt {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    text-transform: uppercase;
    cursor: pointer; }
