.header {
    padding: 30px 0;
    background-color: #fff;
    width: 100%;
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
    @include r($desktop) {
        padding-top: 20px;
        padding-bottom: 20px; }
    @include r($mobile) {
        padding-top: 15px;
        padding-bottom: 15px; } }
.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between; }
.header__nav {
    display: flex;
    align-items: center;
    @include r($desktop) {
        position: absolute;
        top: 100%;
        left: 0;
        transform: scale(0);
        transform-origin: 0 0;
        border-top: 1px solid rgba(#000,0.5);
        flex-direction: column;
        align-items: center;
        background-color: #fff;
        padding: 15px;
        height: calc(var(--vh)*100 - var(--headerHeight));
        overflow-y: auto;
        width: 100%; } }
.header__nav_transition {
    transition: 0.2s transform; }
.header__nav.active {
    @include r($desktop) {
        transform: scale(1); } }
.header__logo {
    max-width: 198px;
    width: 100%;
    display: block;
    @include r($mobile) {
        max-width: 160px; }
    img {
        display: block;
        width: 100%; } }
.header__menu {
    display: flex;
    align-items: center;
    @include r($desktop) {
        flex-direction: column;
        align-items: center; } }
.header__item {
    position: relative;
    margin-left: 36px;
    @include r($wide) {
        margin-left: 30px; }
    @include r($desktop) {
        margin-top: 16px;
        margin-left: 0;
        display: flex;
        flex-direction: column;
        align-items: center; }
    &:first-child {
        margin-left: 0;
        @include r($desktop) {
            margin-top: 0; } }
    @include rmin($desktop+1) {
        &:hover {
            .header__wrap-sub-menu {
                display: block; } }
        &::before {
            display: block;
            content: '';
            position: absolute;
            bottom: -11px;
            left: 0;
            width: 100%;
            height: 3px;
            background-color: #D32F2F;
            opacity: 0;
            transition: 0.3s opacity; }
        &:hover {
            &::before {
                    opacity: 1; } } } }
.header__link {
    position: relative; }
.header__sub-arrow {
    display: none;
    @include r($desktop) {
        display: block;
        background: url(/images/arrow-down.svg) no-repeat center / 100% auto;
        width: 12px;
        height: 12px;
        position: absolute;
        top: 50%;
        left: calc( 100% + 10px );
        transform: translateY(-50%);
        transition: 0.3s transform; } }
.header__sub-arrow_select {
    transform: translateY(-50%) rotate(180deg); }
.header__links {
    margin-left: 120px;
    display: flex;
    align-items: center;
    @include r($wide) {
        margin-left: 100px; }
    @include r($desktop) {
        margin-top: 20px;
        margin-left: 0;
        flex-direction: column;
        align-items: center; } }
.header__link, .header__login-txt, .header__sub-link {
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #212121; }
.header__login-txt, .header__become-partner {
    position: relative;
    @include rmin($desktop+1) {
        &::before {
            display: block;
            content: '';
            position: absolute;
            bottom: -11px;
            left: 0;
            width: 100%;
            height: 3px;
            background-color: #D32F2F;
            opacity: 0;
            transition: 0.3s opacity; }
        &:hover {
            &::before {
                opacity: 1; } } } }
.header__wrap-sub-menu {
    position: absolute;
    top: 100%;
    left: -30px;
    padding-top: 32px;
    width: 200px;
    display: none;
    @include r($wide) {
        padding-top: 32px; }
    @include r($desktop) {
        position: static;
        padding-top: 10px;
        max-width: 342px;
        width: 100%; } }
.header__sub-menu {
    padding-top: 10px;
    background: rgb(248, 248, 248);
    border-radius: 0px 0px 10px 10px;
    @include r($desktop) {
        padding-top: 0;
        border-radius: 10px; } }
.header__sub-link {
    padding-left: 27px;
    padding-right: 27px;
    padding-top: 6px;
    padding-bottom: 8px;
    display: block;
    transition: 0.3s;
    &:hover {
        background: #D32F2F;
        color: #fff; } }
.header__sub-item {
    &:last-child {
        .header__sub-link {
            border-radius: 0 0 10px 10px; } }
    &:first-child {
        @include r($desktop) {
            .header__sub-link {
                border-radius: 10px 10px 0 0; } } } }
.header__become-partner {
    margin-left: 32px;

    font-family: 'Poppins';
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    color: #D32F2F;
    @include r($wide) {
        margin-left: 30px; }
    @include r($desktop) {
        margin-left: 0;
        margin-top: 20px; } }
.header__login {
    position: relative;
    @include r($desktop) {
        display: flex;
        flex-direction: column;
        align-items: center; }
    @include rmin($desktop+1) {
        &:hover {
            .header__wrap-sub-menu {
                display: block; } } } }
.header__login-txt {
    cursor: pointer; }

.burger {
    display: none;
    width: 28px;
    height: 18px;
    margin-left: auto;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    position: relative;
    @include r($desktop) {
        display: flex; }
    .icon-bar {
        transition: 0.1s;
        width: 100%;
        height: 2px;
        background-color: rgba(211, 47, 47, 1);
        @include r($desktop) {
            display: flex; } } }
.burger.active {
    .icon-bar:nth-child(1), .icon-bar:nth-child(3) {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 90%; }
    .icon-bar:nth-child(2) {
        opacity: 0; }
    .icon-bar:nth-child(1) {
        transform: translate(-50%, -50%) rotate(45deg); }
    .icon-bar:nth-child(3) {
        transform: translate(-50%, -50%) rotate(-45deg); } }

.header_congratulations {
    position: relative; }
