.review {
    min-height: 200px;
    width: 100%;
    display: flex;
    align-items: flex-start;
    @include r($mobile) {
        flex-direction: column;
        align-items: center;
        padding-top: 10px; } }
.review__info {
    padding-left: 30px;
    padding-top: 15px;
    @include r($mobile) {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 0; } }
.review__name {
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($tablet) {
        font-size: 20px;
        line-height: normal; }
    @include r($mobile) {
        text-align: center; } }
.review__stars-rating {
    margin-top: 4px; }
.review__txt {
    margin-top: 14px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.2px;
    color: #212121;
    @include r($mobile) {
        margin-top: 10px; } }
.review__img {
    border-radius: 10px; }
