.form-field {
    input[type="text"], input[type="tel"], input[type="email"], input[type="password"], textarea, select {
        width: 100%; } }
.form-field__name {
    margin-bottom: 11px;

    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #000000;
    @include r($tablet) {
        font-size: 16px;
        line-height: normal; } }
.form-field_has-btn-del {
    .form-field__name {
        padding-left: 32px;
        position: relative; }
    .form-field__delete {
        border: none;
        background: none;
        background: url(/images/x.svg) no-repeat center / 100%;
        width: 16px;
        height: 16px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        &:focus {
            outline: none; } } }
.form-field_date {
    position: relative;
    svg {
        display: block;
        position: absolute;
        bottom: 14px;
        right: 12px;
        width: 24px;
        height: 24px;
        fill: #979797;
        transition: 0.3s #D32F2F; } }
.form-field_date_active {
    svg {
        fill: #D32F2F; } }
