.main-banner {
    margin-top: var(--headerHeight);
    padding-top: 225px;
    min-height: 812px;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
    @include r($wide) {
        min-height: 600px;
        padding-top: 150px; }
    @include r($tablet) {
        min-height: auto;
        padding-top: 100px;
        padding-bottom: 150px; }
    @include r($mobile) {
        padding-top: 45px;
        padding-bottom: 45px;
        background: none !important; }
