 }    //     padding-bottom: 45px
.main-banner__title {
    font-family: 'Poppins';
    font-weight: bold;
    font-size: 40px;
    line-height: 60px;
    letter-spacing: -0.005em;
    text-transform: capitalize;
    color: #212121;
    @include r($mobile) {
        font-size: 30px;
        line-height: normal; } }
.main-banner__subtitle {
    margin-top: 12px;
    font-family: 'Poppins';
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    text-transform: capitalize;
    color: #212121; }
.main-banner__form {
    margin-top: 36px;
    @include r($mobile) {
        margin-top: 25px; } }
