.how-it-work {
    margin-top: 140px;
    @include r($wide) {
        margin-top: 100px; }
    @include r($tablet) {
        margin-top: 50px; }
    @include r($mobile) {
        margin-top: 0; } }
.how-it-work__subtitle {
    margin-top: 18px;
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    text-align: center;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($mobile) {
        font-size: 18px;
        line-height: normal; }
    @include r($tablet) {
        margin-top: 0; } }
.how-it-work__txt {
    margin-top: 18px;
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($mobile) {
        font-size: 16px;
        line-height: normal; } }
.how-it-work__step-by-step {
    margin-top: 60px;
    @include r($wide) {
        margin-top: 40px; }
    @include r($tablet) {
        margin-top: 20px; } }



