.faq-item {
    width: 100%;
    padding: 24px;
    background: #F9F9F9;
    border-radius: 20px;
    margin-top: 28px;
    @include r($mobile) {
        padding: 16px; }
    &:first-child {
        margin-top: 0; } }
.faq-item__title {
    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    color: #212121;
    @include r($tablet) {
        font-size: 24px;
        line-height: normal; }
    @include r($mobile) {
        font-size: 18px; } }
.faq-item__content {
    margin-top: 16px;
    max-width: 800px;
    width: 100%;
    display: none;
    p, ul, ol {
        margin-top: 24px;
        @include r($tablet) {
            margin-top: 15px; }
        &:first-child {
            margin-top: 0; } }
    p, li, a {
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: 0.2px;
        color: #212121; }
    a {
        text-decoration: underline; }
    li {
        padding-left: 24px;
        position: relative;
        @include r($tablet) {
            padding-left: 20px; } }
    ul {
        li {
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 13px;
                font-weight: normal;
                font-size: 14px;
                line-height: 24px;
                letter-spacing: 0.2px;
                color: #212121;
                @include r($tablet) {
                    left: 8px; } } } } }
.faq-item__btn {
    font-family: Poppins;
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    margin-top: 16px;
    display: flex;
    align-items: center;
    @include r($mobile) {
        font-size: 14px;
        line-height: 14px; }
    &::after {
        content: url(/images/arrow-right.svg);
        width: 18px;
        margin-left: 12px;
        margin-top: 2px; }
    &::before {
        content: url(/images/arrow-right.svg);
        width: 18px;
        margin-right: 12px;
        margin-top: 2px;
        transform: rotateY(180deg);
        display: none; } }
.faq-item_open {
    .faq-item__btn {
        &::after {
            display: none; }
        &::before {
            display: block; } } }
