.article__go-back {
    width: 48px;
    display: block;
    &::before {
        content: url(/images/arrow-left.svg);
        width: 100%;
        opacity: 0.9;
        transition: 0.3s opacity; }
    &:hover {
        &::before {
            opacity: 1; } } }
.article__section-title {
    text-align: left;
    margin-top: 52px;
    @include r($desktop) {
        margin-top: 20px; } }
.article__img {
    width: 100%;
    height: auto;
    display: block;
    margin-top: 40px; }
.article__inner {
    margin-top: 45px;
    h3,p {
        margin-top: 24px;
        @include r($tablet) {
            margin-top: 18px; }
        &:first-child {
            margin-top: 0; } }
    p {
        font-weight: normal;
        font-size: 18px;
        line-height: 28px;
        letter-spacing: -0.005em;
        color: #212121;
        @include r($tablet) {
            font-size: 16px;
            line-height: normal; } }
    h3 {
        font-style: normal;
        font-weight: 600;
        font-size: 24px;
        line-height: 36px;
        letter-spacing: -0.005em;
        color: #D32F2F;
        @include r($tablet) {
            font-size: 21px;
            line-height: normal; } } }
