.steps-forms__offers-for-moving_1, .steps-forms__offers-for-moving_2, .steps-forms__offers-for-moving_3 {
    .steps-forms__block {
        margin-top: 35px;
        &:first-child {
            margin-top: 0; } }
    .steps-forms__wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        max-width: 820px;
        width: 100%;
        @include r($mobile) {
            flex-direction: column; } }
    .form-field {
        width: calc( 50% - 40px );
        margin-top: 40px;
        @include r($desktop) {
            width: calc(50% - 15px);
            margin-top: 20px; }
        @include r($mobile) {
            width: 100%; }
        @include rmin($mobile+1) {
            &:nth-child(-n+2) {
                margin-top: 0; } }
        @include r($mobile) {
            &:first-child {
                margin-top: 0; } } }
    .steps-form__checkboxes {
        .row {
            & > div {
                margin-top: 18px; } } } }
