.company-info__container {
    position: relative; }
.company-info__section-title {
    text-align: left; }
.company-info__logo {
    max-width: 270px;
    height: auto;
    position: absolute;
    right: 15px;
    top: 40px;
    @include r($desktop) {
        position: static;
        max-width: 200px;
        margin-top: 30px; }
    @include r($tablet) {
        margin-top: 15px; } }
.company-info__contact {
    margin-top: 28px;
    display: flex;
    flex-direction: column;
    align-items: flex-start; }
.company-info__contact-item {
    margin-top: 16px;
    padding-left: 44px;
    position: relative;
    font-family: 'Poppins';
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.005em;
    text-transform: capitalize;
    color: #212121;
    @include r($mobile) {
        padding-left: 0;
        font-size: 18px;
        line-height: normal; }
    &:first-child {
        margin-top: 0; }
    &::before {
        width: 30px;
        position: absolute;
        top: calc( 50% + 5px );
        left: 0;
        transform: translateY(-50%);
        @include r($mobile) {
            display: none; } } }
.company-info__contact-item_tel {
    &::before {
        content: url(/images/call_red.svg);
        @include r($mobile) {
            content: ''; } } }
.company-info__contact-item_email {
    &::before {
        content: url(/images/msg_red.svg);
        @include r($mobile) {
            content: ''; } } }
.company-info__block {
    margin-top: 49px;
    @include r($tablet) {
        margin-top: 30px; } }
.company-info__block-title {
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    letter-spacing: -0.005em;
    color: #D32F2F; }
.company-info__items {
    & > div {
        margin-top: 24px;
        @include r($tablet) {
            margin-top: 20px; } } }
.company-info__item {
    font-weight: 600;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    color: #000000;
    padding-left: 32px;
    position: relative;
    @include r($tablet) {
        font-size: 16px;
        line-height: normal; }
    &::before {
        content: url(/images/checked-icon.svg);
        width: 20px;
        position: absolute;
        top: 3px;
        left: 0; } }

.company-info__reviews {
    margin-top: 25px;
    padding-right: 30px;
    max-height: 682px;
    .simplebar-track.simplebar-vertical {
        width: 4px;
        border-radius: 4px; }
    .simplebar-scrollbar:before {
        left: 0;
        right: 0;
        opacity: 1;
        background-color: #D32F2F; }
    .simplebar-track.simplebar-vertical .simplebar-scrollbar:before {
        top: 0;
        bottom: 0; } }
.company-info__review {
    margin-top: 40px;
    &:first-child {
        margin-top: 0; } }
.company-info__feedback-form {
    margin-top: 30px; }
