.blog-card {
    width: 100%;
    min-height: 387px;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background: #F9F9F9;
    border-radius: 20px;
    @include r($desktop) {
        min-height: auto; } }
.blog-card__img {
    height: 266px;
    width: 100%;
    object-fit: cover;
    @include r($desktop) {
        height: 200px; } }
.blog-card__title {
    margin-bottom: 16px;
    padding-left: 24px;
    padding-right: 24px;
    padding-top: 24px;

    font-weight: 500;
    font-size: 28px;
    line-height: 42px;
    color: #212121;
    @include r($wide) {
        font-size: 24px;
        line-height: normal; }
    @include r($tablet) {
        font-size: 20px;
        padding-top: 12px;
        padding-left: 12px;
        padding-right: 12px;
        margin-bottom: 8px; } }
.blog-card__read-more {
    margin-bottom: 24px;
    margin-left: 24px;

    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #D32F2F;
    display: flex;
    align-items: center;
    @include r($tablet) {
        margin-left: 12px;
        margin-bottom: 12px; }
    @include r($mobile) {
        font-size: 14px;
        line-height: 14px; }
    &::after {
        content: url(/images/arrow-right.svg);
        width: 18px;
        margin-left: 12px;
        margin-top: 2px; } }
