.contact-us {
    margin-top: 200px;
    margin-bottom: 140px;
    @include r($desktop) {
        margin-bottom: 100px;
        margin-top: 70px; }
    @include r($tablet) {
        margin-bottom: 50px; } }
.contact-us__section-title {
    @include r($mobile) {
        font-size: 25px; } }
.contact-us__form {
    margin-top: 48px;
    margin-left: auto;
    margin-right: auto;
    @include r($mobile) {
        margin-top: 35px; } }
