.congratulations {
    padding-top: 300px;
    padding-bottom: 300px;
    background: url(/images/congratulations-decor.png) no-repeat center;
    @include r($wide) {
        padding-top: 45px;
        padding-bottom: 45px;
        background: none; } }
.congratulations__container {
    position: relative;
    .section-title {
        position: relative;
        z-index: 2; }
    .congratulations__subtitle {
        margin-top: 22px;
        @include r($desktop) {
            margin-top: 0; } } }
