.footer {
    background-color: #D32F2F;
    padding-top: 56px;
    padding-bottom: 27px;
    position: relative;
    overflow: hidden;
    @include r($mobile) {
        padding-top: 45px; }
    &::before {
        content: url(/images/footer-bg.png);
        position: absolute;
        top: 0;
        right: 0;
        @include r(1439) {
            display: none;
            content: ''; } } }
.footer__row {
    margin-top: 30px;
    &:first-child {
        margin-top: 0; } }
.footer__row_second {
    position: relative;
    display: flex;
    @include r($tablet) {
        flex-direction: column; } }
.footer__wrap_lists {
    max-width: 175px;
    @include r($tablet) {
        max-width: 100%; } }
.footer__logo {
    max-width: 325px;
    width: 100%;
    height: auto;
    display: block;
    @include r($mobile) {
        max-width: 270px; } }
.footer__list {
    margin-top: 20px;
    &:first-child {
        margin-top: 0; } }
.footer__list-item {
    & > a {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 0.2px;
        color: #FFFFFF; } }
.footer__list-item_has-sublist {
    & > a {
        position: relative; } }
.footer__sublist {
    display: none;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 10px;
    background-color: transparent; }
.footer__sublist-item {
    margin-top: 10px;
    a {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 0.2px;
        color: #fff;
        &:hover {
            text-decoration: underline; } } }
.footer__wrap_contact {
    margin-left: 57px;
    display: flex;
    flex-direction: column;
    @include r($tablet) {
        margin-left: 0;
        margin-top: 30px; } }
.footer__contact-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    text-transform: capitalize;
    color: #FFFFFF; }
.footer__contact-links {
    margin-top: 23px;
    display: flex;
    flex-direction: column; }
.footer__contact-link {
    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: -0.005em;
    text-transform: lowercase;
    color: #FFFFFF;
    padding-left: 37px;
    position: relative;
    margin-top: 14px;
    &:first-child {
        margin-top: 0; }
    &:hover {
        text-decoration: underline; }
    &::before {
        width: 30px;
        position: absolute;
        top: calc( 50% + 2px );
        left: 0;
        transform: translateY(-50%); } }
.footer__contact-link_email {
    &::before {
        content: url(/images/msg.svg); } }
.footer__contact-link_tel {
    &::before {
        content: url(/images/call.svg); } }

.footer__contact-soc {
    display: flex;
    position: absolute;
    right: 0;
    bottom: -10px;
    @include r(1439) {
        bottom: 50%;
        transform: translateY(50%); }
    @include r($tablet) {
        bottom: 0;
        position: static;
        margin-top: 5px; } }
.footer__contact-soc-item {
    max-width: 30px;
    max-height: 22px;
    margin-left: 36px;
    @include r($desktop) {
        margin-left: 20px; }
    &:first-child {
        margin-left: 0; }
    svg {
        max-width: 100%;
        max-height: 100%;
        display: block;
        fill: #fff; } }

.footer__row_third {
    display: flex;
    margin-top: 50px;
    position: relative;
    @include r($tablet) {
        flex-direction: column;
        align-items: flex-start;
        margin-top: 30px; } }
.footer__copy, .footer__link {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.2px;
    color: #FFFFFF; }
.footer__links {
    margin-left: 48px;
    @include r($tablet) {
        margin-left: 0;
        margin-top: 15px; } }
.footer__link {
    margin-left: 48px;
    &:hover {
        text-decoration: underline; }
    &:first-child {
        margin-left: 0; } }

.footer__sub-arrow {
    display: block;
    background: url(/images/arrow-down.svg) no-repeat center / 100% auto;
    width: 15px;
    height: 10px;
    position: absolute;
    top: 50%;
    left: calc( 100% + 10px );
    transform: translateY(-50%);
    transition: 0.3s transform;
    filter: invert(1); }
.footer__sub-arrow_select {
    transform: translateY(-50%) rotate(180deg); }
.footer_login {
    padding-top: 30px;
    padding-bottom: 30px;
    &::before {
        content: '';
        display: none; } }
.footer__l-link, .footer__l-txt {
    font-weight: normal;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF; }
.footer__l-txt {
    @include r($desktop) {
        margin-top: 10px; }
    a {
        font-weight: bold;
        font-size: inherit;
        line-height: inherit;
        color: inherit;
        &:hover {
            text-decoration: underline; } } }
.footer__l-link {
    &:hover {
        text-decoration: underline; } }
