.step-by-step {
    counter-reset: i;
    display: flex;
    justify-content: space-between;
    @include r($wide) {
        justify-content: center; } }
.step-by-step__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    @include r($wide) {
        margin-top: 40px; }
    &:last-child {
        &::after {
            display: none; } }
    &:first-child {
        @include r($wide) {
            margin-top: 0; } }
    &::after {
        content: '';
        display: block;
        position: absolute;
        top: 135px;
        left: 110%;
        height: 26px;
        width: 80px;
        background: url(/images/arrow-right_step.svg) no-repeat center right / 100% auto;
        @include r($wide) {
            display: none;
            background: none; } } }
.step-by-step__item-counter {
    width: calc( 100% / 3 );
    &::before {
        content: counter(i);
        counter-increment: i;

        font-family: 'Poppins';
        font-weight: bold;
        font-size: 80px;
        line-height: 120px;
        letter-spacing: -0.005em;
        text-transform: capitalize;
        color: #D32F2F;
        @include r($mobile) {
            line-height: 100px; } } }
.step-by-step__item-title {
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -0.005em;
    color: #212121;
    margin-top: 10px;
    @include r($wide) {
        margin-top: 5px; }
    @include r($mobile) {
        font-size: 22px;
        line-height: normal;
        margin-top: 5px; } }
.step-by-step__item-txt {
    margin-top: 20px;

    font-weight: normal;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.005em;
    color: #212121;
    @include r($mobile) {
        font-size: 16px;
        line-height: normal;
        margin-top: 15px; } }
