* {
  box-sizing: border-box;
  outline: none; }

body, html {
  background: white;
  color: $fontColor;
  font-family: $font-family;
  font-size: 16px;
  letter-spacing: -0.005em;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-transform: capitalize; }

.body_hidden {
  overflow: hidden; }

.page-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  width: 100%; }

h1,h2,h3,h4,h5,h6,p,a,span,ul,ol,li {
  padding: 0;
  margin: 0; }
a {
  color: $fontColor;
  text-decoration: none;
  font-family: $font-family;
  font-weight: 400;
  &:hover, &:focus {
    text-decoration: none; } }
ul {
  list-style: none;
  li {} }
ol {
  list-style: decimal; }
.section-title {
  font-family: $font-family;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  text-align: center;
  letter-spacing: -0.005em;
  text-transform: capitalize;
  color: #212121;
  @include r($desktop) {
    font-size: 35px;
    line-height: normal; }
  @include r($mobile) {
    font-size: 28px; } }
.section-title_color_red {
  color: #D32F2F; }
.section-title_color_white {
  color: #fff; }

.headerHeightMarginTop {
  margin-top: var(--headerHeight); }

.go-back-btn {
  width: 30px;
  display: block;
  &::before {
    content: url(/images/arrow-left.svg);
    width: 100%; } }
.go-back-btn_margin_bottom {
  margin-bottom: 45px;
  @include r($wide) {
    margin-bottom: 40px; }
  @include r($desktop) {
    margin-bottom: 35px; }
  @include r($tablet) {
    margin-bottom: 25px; } }

.defaultPaddings {
  padding-top: 112px;
  padding-bottom: 120px;
  @include r($wide) {
    padding-top: 70px;
    padding-bottom: 70px; }
  @include r($desktop) {
    padding-top: 60px;
    padding-bottom: 60px; }
  @include r($tablet) {
    padding-top: 50px;
    padding-bottom: 50px; } }
.formBtnMarginTop {
  margin-top: 48px;
  @include r($desktop) {
    margin-top: 40px; }
  @include r($tablet) {
    margin-top: 30px; }
  @include r($mobile) {
    margin-top: 25px; } }
